import { Component, OnInit, AfterViewInit, ViewEncapsulation } from '@angular/core';
import { SubDomain } from '../../../shared/interfaces/subdomain.interface';
import { CompanyService } from '../../../shared/services/index';
import { Router } from '@angular/router';
import { OverviewComponent, UserDetailsComponent, TrafficDetailsComponent } from './components/index';
import { Script } from '../../../shared/services/index';
import { Item, App } from './../../+builder/models/model';
declare var google: any;
declare var jQuery: any;
declare var moment: any;

@Component({
  selector: 'og-analytics',
  template: require('./analytics.component.html'),
  styles: [
    require('./assets/css/daterangepicker.css'),
    require('./assets/css/analytics.component.css')
  ],
  encapsulation: ViewEncapsulation.None,
})

export class AnalyticsComponent implements OnInit, AfterViewInit {
  subDomain: SubDomain;
  company_id: String;
  live_calculators: any = [];
  analytic_component: String = '';
  calc_id: String;
  calc_name: String;
  private activeSince: any;
  private isActive: any;
  private calculator: App;

  constructor(private companyService: CompanyService,
              public _router: Router,
              private _script: Script) {
    this.company_id = localStorage.getItem('company');
  }

  ngAfterViewInit() {

    this._script.load('jqueryUI', 'morrisCharts', 'datatables', 'daterangepicker')
      .then((data) => {
        console.log('Scripts Loaded', data);
        this.analytic_component = "overview";
      })
      .catch((error) => {
        //any error
      });
  }

  ngOnInit() {
    let self = this;
    //Code
    this.companyService.getLiveCompanyProjects(this.company_id)
      .subscribe(
        (response: any) => {
          if (response.length) {
            self.calculator = response[0];
            this.live_calculators = response;
            this.calc_id = response[0].parentApp;
            this.calc_name = response[0].name;
            this.activeSince = moment(response[0].createdAt).fromNow().replace('ago', '').trim();
            this.isActive = response[0].mode == 'PUBLIC';
          } else {
            this.calc_id = 'null';
          }
        },
        (error: any) => {
          console.log(error);
        }
      );
    jQuery('.slimscroll').slimscroll({
      railVisible: true,
      alwaysVisible: true
    });
  }

  onAnalyticTypeSelect(type: String) {
    this.analytic_component = type;
  }

  onCalcSelect(calc: any) {
    this.calculator = calc;
    this.calc_id = calc.parentApp;
    this.calc_name = calc.name;
    this.activeSince = moment(calc.createdAt).fromNow().replace('ago', '').trim();
    this.isActive = calc.mode == 'PUBLIC';
  }

}



